.loadButton {
  font-size: 15px;
  display: block;
  margin-top: 40px;
  margin-bottom: 10px;
  margin-left: auto;
  margin-right: auto;
  padding: 20px 40px;
  border: 3px solid transparent;
  background-color: rgb(52, 143, 0);
  color: #fff;
  border-radius: 6px;
  box-shadow: 0 2px 1px 0 rgba(3, 3, 3, 0.384);
  transition: background-color 300ms ease, color 300ms ease, border 300ms ease;
}

.loadButton:hover {
  border: 3px solid rgb(52, 143, 0);
  background-color: #fff;
  color: rgb(29, 78, 0);
}

.loaderContainer {
  margin-top: 40px;
  margin-bottom: 10px;
  width: 50px;
  margin-left: auto;
  margin-right: auto;
}

.modalImage {
  border-radius: 7px;
  margin: 0;
  padding: 0;
  display: block;
  width: 1000px;
}
