body {
  padding: 0;
  margin: 0;
}

.header {
  margin: 0;
  text-align: center;
  background-color: rgb(52, 143, 0);
  padding-top: 20px;
  padding-bottom: 20px;
}

.headerInput {
  height: 40px;
  width: 300px;
  border: none;
  border-radius: 4px;
  margin-right: 30px;
  padding-left: 15px;
  box-shadow: 0 3px 1px 0 rgba(3, 3, 3, 0.384);
}

.headerbutton {
  color: rgb(29, 78, 0);
  background-color: #fff;
  padding: 11px 30px;
  border-radius: 4px;
  border: 2px solid transparent;
  box-shadow: 0 3px 1px 0 rgba(3, 3, 3, 0.384);
  transition: background-color 300ms ease, color 300ms ease, border 300ms ease;
}

.headerbutton:hover {
  background-color: rgb(52, 143, 0);
  border: 2px solid #fff;
  color: #fff;
}
