.galeryContainer {
  width: 1260px;

  margin: 0;
  padding: 0;

  margin-left: auto;
  margin-right: auto;
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  grid-column-gap: 30px;
  grid-row-gap: 30px;
  margin-top: 50px;
  list-style: none;
}

.galeryItem {
  margin: 0;
  padding: 0;
  height: 260px;
}

.galeryImage {
  border-radius: 7px;
  display: block;
  width: 100%;
  height: 100%;
  object-fit: cover;
  object-position: center;
  margin: 0;
  padding: 0;
}
